@import "tipografia";
@import "colores";

*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	list-style: none;

}
body{
	background-color:$color-secundario;
	font-family: $font;
}
.header {
	background-color: $color-principal;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	input{
		width: 25%;
		height: 30px;
	}
	img{
		padding: 10px 10px;
		height: 100px;
		}
}

/*navbar con animacion*/
.navbar__menu {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: row;
}

.navbar__menu-link {
  display: block;
  text-decoration: none;
  color: inherit;
  padding: 5px 10px;
  margin-right: 10px;
  position: relative;
}

.navbar__menu-link:after {
	content: "";
	width: 0;
	height: 4px;
	background-color: #011C26;
	position: absolute;
	bottom: 0;
	right: 50%;		
	transition: all 0.5;
	will-change: width;
	}

.navbar__menu-link:hover:after {
		width: 100%;
		right: 0;
}

/*MIXINS*/

@mixin box-base($alineacion, $color-principal){
	background-color: $color-principal;
	margin: 50px;
	border-radius:15px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: $alineacion;
	padding: 10px;
	img{
		width: 300px;
		height: 200px;
	}
}

.box1{
	@include box-base(space-between, #03738C);
}

.box2{
	@include box-base(space-between, #03738C);
}

.box3{
	@include box-base(space-between, #03738C);
}

.btn-base{
	text-decoration: none;
	padding: 5px 10px;
	border-radius:10px;
	font-size: $fontMedium;
	color: $color-secundario;
}
/*EXTEND*/

.btn-box1{
	@extend .btn-base;
	background-color: #011C26;
}

.btn-box2{
	@extend .btn-base;
	background-color: #011C26;
}

.btn-box3{
	@extend .btn-base;
	background-color: #011C26;

}

.grid-layout{	
	display: grid;
	grid-template-columns: repeat(3,1fr);
	justify-content: space-around;
	width: 80%;
	margin: 500px auto 200px;
	padding: 0px;
}

.footer{
	background-color: $color-principal;
		margin: 0;
		padding: 0px 0px;
		b{
		display: inline-block;
		padding-top: 10px;
		padding-left: 15px;	
		}
		h2{
			display: inline-block;
		padding-top: 10px;
		padding-left: 15px
		}
		p{
			color:$color-secundario;
			margin-bottom: 0;
		}
}

$redesSociales:(
	whatsapp: lightgreen,
	instagram: #c92d8f,
);

@each $nombre, $color in $redesSociales{
	.link-#{$nombre}{
		margin: 0 5px;
		padding: 5px;
		border-radius:10px;
		color: black;
		text-decoration: none;
		background-color:$color;
	}
}

